import React from "react";
//
import { IconButton } from "../Common/IconButton";
import {
	SeaBattleBoatData,
	AngleToDirection
} from "../../utils/games/seabattle";
import { SeaBattleMovementIconMappings } from "../../utils/games/seabattle/mappings";
import { useTranslation } from "react-i18next";

// ------------------------------------------------------------------

export const PlayerControls = ({
	boat,
	disabled = false,
	onPlayNext,
	onMoveBackward,
	onMoveForward,
	onRotateLeft,
	onRotateRight
}: {
	boat?: SeaBattleBoatData;
	disabled?: boolean;
	onPlayNext?: () => void;
	onMoveBackward: () => void;
	onMoveForward: () => void;
	onRotateLeft: () => void;
	onRotateRight: () => void;
}) => {
	const { t } = useTranslation();

	return (
		<div className="SeaBattleControls">
			<IconButton
				disabled={disabled}
				icon="rotate-left"
				title="Rotate Left"
				onClick={onRotateLeft}
			/>
			<IconButton
				disabled={disabled}
				icon={
					boat
						? SeaBattleMovementIconMappings[
								AngleToDirection(boat.angle)
						  ].backward
						: "arrow-down"
				}
				title="Move Backward"
				onClick={onMoveBackward}
			/>
			<IconButton
				icon="step-forward"
				onClick={onPlayNext}
				size="M"
				title={t("player.forward")}
			/>
			<IconButton
				disabled={disabled}
				icon={
					boat
						? SeaBattleMovementIconMappings[
								AngleToDirection(boat.angle)
						  ].forward
						: "arrow-up"
				}
				title="Move Forward"
				onClick={onMoveForward}
			/>
			<IconButton
				disabled={disabled}
				icon="rotate-right"
				title="Rotate Right"
				onClick={onRotateRight}
			/>
		</div>
	);
};
